import { Typography } from "@mui/material"
import { WatchlistDataTableTextCell } from "../../WatchlistDataTableCells/WatchlistDataTableTextCell"
import { genericColumn } from "../../genericColumn"

function reportingDate(_tableName: string) {
  return genericColumn({
    endpoint: "companyLatestSurprise",
    columnKeys: ["reportingDate"],
    size: 120,
    title: "Reported Date",
    enableSorting: true,
    enableHiding: true,
    cell: info => {
      return (
        <WatchlistDataTableTextCell
          queryResult={info.row.original.financialData.companyLatestSurprise}
          value={info.row.original.financialData.companyLatestSurprise?.data?.reportingDate}
        >
          <Typography>
            {info.row.original.financialData.companyLatestSurprise?.data?.reportingDate}
          </Typography>
        </WatchlistDataTableTextCell>
      )
    },
  })
}

export { reportingDate }
